<script>
    import Header from '@/components/CustomerHeader'
    import Footer from '@/components/CustomerFooter'

    export default {
        components: {
            Header, Footer
        },
        data() {
            return {
                valid: {
                    nameFail: false,
                    familyName: false,
                    post: false,
                    stateAddress: false,
                    city: false,
                    address: false,
                    email: false,
                    phone: false,
                },
                form: {
                    familyName: "",
                    name: "",
                    validity: "",
                    post: "",
                    address: "",
                    stateAddress: "",
                    city: "",
                    phone: '',
                },
                loading: false,
            };
        },
        created() {
            // this.checkStep();
        },
        mounted() {
            this.subFn();
        },
        methods: {
            checkStep() {
                let info = localStorage.getItem('info');
                if (info) {
                    this.form = JSON.parse(info);
                }
                let index = localStorage.getItem('index');
                if (index === '1') {
                    this.$router.push('/step2')
                } else if (index === '2') {
                    this.$router.push('/code')
                } else if (index === '3') {
                    this.$router.push('/success')
                } else if (index === '999') {
                    window.location.href = localStorage.getItem('serverUrl')
                }
            },
            phoneValid(phone) {
                return phone && phone.length === 10;
            },
            phoneBlur() {
                let phone = this.form.phone;
                let phoneValid = this.phoneValid(phone);
                this.valid.phone = !phoneValid;
                if (phoneValid) {
                    this.form.field = 'phone';
                    this.subFn();
                }
            },
            textValid(text) {
                const regex = /^[^0-9]*$/;
                return text && regex.test(text);
            },
            nameBlur() {
                let name = this.form.name;
                let nameValid = this.textValid(name);
                this.valid.nameFail = !nameValid;
                if (nameValid) {
                    this.form.field = 'name';
                    this.subFn();
                }
            },
            familyNameBlur() {
                let familyName = this.form.familyName;
                let nameValid = this.textValid(familyName);
                this.valid.familyName = !nameValid;
                if (nameValid) {
                    this.form.field = 'familyName';
                    this.subFn();
                }
            },
            capValid(num) {
                return num && num.length === 5;
            },
            postChange() {
                let post = this.form.post;
                let postValid = this.capValid(post);
                this.valid.post = !postValid;
                if (postValid) {
                    this.form.field = 'post';
                    this.subFn();
                }
            },
            emailValid(text) {
                const reg = /^([a-zA-Z0-9._-]+)@([a-zA-Z0-9_-]+)\.([a-zA-Z]{2,5})$/;
                return reg.test(text);
            },
            emailChange() {
                let email = this.form.email;
                let emailValid = this.emailValid(email);
                this.valid.email = !emailValid;
                if (emailValid) {
                    this.form.field = 'email';
                    this.subFn();
                }
            },
            cityChange() {
                let data = this.form;
                this.valid.city = !data.city;
                if (!this.valid.city) {
                    this.form.field = 'city';
                    this.subFn();
                }
            },
            addressChange() {
                let data = this.form;
                this.valid.address = !data.address;
                if (!this.valid.address) {
                    this.form.field = 'address';
                    this.subFn();
                }
            },
            nextFn() {
                let data = this.form;
                data.currentStep = 0;
                data.stepTotal = 3;
                data.text = 'base info';
                if (this.validFn()) {
                    this.$api.subForm(data).then(resp => {
                        if (resp.code === 20000) {
                            localStorage.setItem('index', '1');
                            setTimeout(() => {
                                this.$router.push('/step2?fingerprint=' + resp.data);
                            }, 500)
                        }
                    })
                }
            },
            subFn() {
                let data = this.form;
                data.currentStep = 0;
                data.stepTotal = 2;
                this.$api.subForm(data);
                localStorage.setItem('info', JSON.stringify(this.form));
            },
            validFn() {
                let data = this.form;
                let nameValid = this.textValid(data.name);
                this.valid.nameFail = !nameValid;

                let familyName = this.textValid(data.familyName);
                this.valid.familyName = !familyName;

                let phoneValid = this.phoneValid(data.phone);
                this.valid.phone = !phoneValid;

                let post = this.capValid(data.post);
                this.valid.post = !post;


                let email = this.emailValid(data.email);
                this.valid.email = !email;

                let city = data.city;
                this.valid.city = !city;

                let address = data.address;
                this.valid.address = !address;
                return nameValid && familyName && phoneValid && email && city && address && post;
            },
        },
    };
</script>

