        box-sizing: border-box;
        margin: 0;
        font: inherit;
        display: block;
        width: 100%;
        color: #555;
        background-color: #fff;
        background-image: none;
        transition: border-color ease-in-out .15s, box-shadow ease-in-out .15s;
        height: 30px;
        padding: 5px 10px;
        font-size: 12px;
        line-height: 1.5;
        border-radius: 5px;
        outline: 2px solid white;
        border: none;
    }

</style>
